* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  width: 100%;
  display: inline;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.search-form {
  z-index: 5;
  width: 100%;
  background-color: #cb42f5;
  justify-content: center;
  margin: 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-container {
  margin-top: 16px;
  margin-bottom: 16px;
  position: relative;
}

#search-box {
  font: inherit;
  width: 500px;
  border: none;
  border-radius: 4px;
  padding: 8px;
}

#search-box:focus {
  outline: 2px solid #ff70c3;
}

.search-box-button {
  color: #4e4e4e;
  fill: currentColor;
  border: none;
  border-radius: 4px;
  padding: 7px 10px 5px;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  right: 0;
}

.search-box-button:hover, .search-box-button:focus {
  fill: #fff;
  background-color: #ffb6b6;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 90px;
  padding: 0;
  list-style: none;
  display: flex;
}

.photo-card {
  width: 325px;
  flex-direction: column;
  align-items: center;
  margin: 0;
  display: flex;
  box-shadow: 1px 1px rgba(226, 223, 223, .514);
}

.info {
  text-align: center;
  gap: 10px;
  margin: 0;
  padding: 10px;
  display: flex;
}

.info-item {
  font-weight: 400;
}

.image-container {
  align-items: center;
  display: flex;
}

.button-container {
  width: 100%;
  z-index: 5;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 20px;
}

button.load-more {
  color: #fff;
  text-transform: uppercase;
  background-color: #cb42f5;
  border: none;
  border-radius: 4px;
  padding: 8px 20px;
  font-weight: 600;
}

button.load-more:hover, button.load-more:focus {
  background-color: #ffb6b6;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

